import React from 'react';
import Bash from '../Components/Highlight/Bash';
import Slide from '../Components/RevealComponents/Slide';
import SlideWithTitle from '../Components/RevealComponents/SlideWithTitle';
import Link from '../Components/Link';

const gitInit = `
git init
`;

const gitClone = `
git clone 
`;

const gitClonePadolabs = `
git clone https://github.com/padolabs/web-development-101.git 
`;

const gitCloneSsh = `
git clone git@github.com:padolabs/web-development-101.git
`;

const gitStatus = `
git status --short
`;

const gitAdd = `
git add .
`;

const gitDiff = `
git diff
`;

const gitCommit = `
git commit -m 'my commit message'
`;

const gitReset = `
git reset
`;

const gitRemote = `
git remote -v
`;

const gitPull = `
git pull
`;

const gitPush = `
git push
`;

const AboutGitCommands = () => (
  <Slide>
    <SlideWithTitle title="Introdução ao Git">
      <h6>A básico sobre Versionamento de código</h6>
      <img width="250px" src="image/git-logo.png" alt="github" />
      <p>https://git-scm.com</p>
    </SlideWithTitle>
    <Slide transition="slide">
      <h4>O que é GIT ?</h4>
      <aside>
        "De longe, o sistema de controle de versão moderno mais usado no mundo
        hoje é o Git. O Git é um projeto de código aberto maduro e com
        manutenção ativa desenvolvido em 2005 por Linus Torvalds, o famoso
        criador do kernel do sistema operacional Linux". Fonte: Atlassian
      </aside>
      <br />
      <div className="row">
        <div className="column">
          <ul>
            <li>Controle de Versão</li>
            <li>Gerênciamento de Histórico</li>
            <li>Rastreio de Alterações</li>
          </ul>
        </div>
        <div className="column">
          <img
            width="175px"
            src="image/git-logo.png"
            alt="QR code that links to these slides"
          />
        </div>
      </div>
    </Slide>
    <Slide transition="slide">
      <h4>Adquirindo e Criando Projetos</h4>
      <aside>
        Existe dois comandos criacionais, você pode adquirir um projeto já
        existênte ou criar um totalmente novo
      </aside>
      <Bash code={gitClone} />
      <Bash code={gitInit} />
    </Slide>
    <Slide transition="slide">
      <h4> Git Clone</h4>
      <Bash code={gitClonePadolabs} />
      <Bash code={gitCloneSsh} />
      <ul>
        <li>
          Clona um repositório em um novo diretório na máquina onde o comando
          foi executado
        </li>
        <li>
          Usado quando já existe um repositório COM CONTEÚDO em uma plataforma
          como o Github
        </li>
      </ul>
    </Slide>
    <Slide transition="slide">
      <h4> Git Init</h4>
      <Bash code={gitInit} />
      <ul>
        <li> Cria um repositório git vazio ou reinicializa um já existente</li>
        <li>
          Usado quando você inicializa um novo projeto, ou quando você deseja
          começar a versionar um projeto já existente em sua máquina
        </li>
      </ul>
    </Slide>
    <Slide transition="slide">
      <h4>Snapshotting Básico</h4>
      <Bash code={gitStatus} />
      <Bash code={gitDiff} />
      <Bash code={gitAdd} />
      <Bash code={gitReset} />
      <Bash code={gitCommit} />
      <aside> "Salvando uma imagem das suas alterações"</aside>
    </Slide>
    <Slide transition="slide">
      <h4> Git Status</h4>
      <p>
        Exibe caminhos que têm diferenças entre o arquivo de índice e o HEAD
        atual, caminhos que têm diferenças entre a árvore de trabalho e o
        arquivo de índice, e caminhos na árvore de trabalho que não são
        rastreados pelo Git (e não são ignorados pelo gitignore).
      </p>
      <Bash code={gitStatus} />
    </Slide>
    <Slide transition="slide">
      <h4> Git Diff</h4>
      <p>
        Mostrar alterações entre a árvore de trabalho e o índice ou uma árvore,
        alterações entre o índice e uma árvore, mudanças entre duas árvores,
        alterações resultantes de uma fusão, alterações entre dois objetos de
        bolha ou alterações entre dois arquivos em disco.
      </p>
      <Bash code={gitDiff} />
    </Slide>
    <Slide transition="slide">
      <h4> Git Add</h4>
      <p>
        Este comando atualiza o índice usando o conteúdo atual encontrado na
        árvore de trabalho, para preparar o conteúdo encenado para o próximo
        compromisso.
      </p>
      <Bash code={gitAdd} />
    </Slide>
    <Slide transition="slide">
      <h4> Git Reset</h4>
      <p>Redefinir o HEAD atual para o estado especificado</p>
      <Bash code={gitReset} />
    </Slide>
    <Slide transition="slide">
      <h4> Git Commit</h4>
      <p>
        Crie um novo commit contendo o conteúdo atual do índice e a mensagem de
        registro dada descrevendo as alterações.
      </p>
      <Bash code={gitCommit} />
    </Slide>
    <Slide transition="slide">
      <h4>O Básico sobre Atualizar Projetos</h4>
      <aside>Os principais comandos para atualizar um projeto são:</aside>
      <Bash code={gitRemote} />
      <Bash code={gitPull} />
      <Bash code={gitPush} />
    </Slide>
    <Slide transition="slide">
      <h4> Git Remote</h4>
      <p>
        Gerencie o conjunto de repositórios ("controles remotos") cujos ramos
        você rastreia.
      </p>
      <Bash code={gitRemote} />
    </Slide>
    <Slide transition="slide">
      <h4> Git Pull</h4>
      <p>
        Incorpora alterações de um repositório remoto no ramo atual. Se o ramo
        atual estiver atrás do controle remoto,
      </p>
      <Bash code={gitPull} />
    </Slide>
    <Slide transition="slide">
      <h4> Git Push</h4>
      <p>
        Atualiza referencias remotas usando referencias locais, enquanto envia
        objetos necessários para completar as referências indicadas.
      </p>
      <Bash code={gitPush} />
    </Slide>
    <Slide transition="slide">
      <div className="column">
        <h4>Aula 2 - Exercícios do 16 ao 26</h4>
        <img
          height="250px"
          width="250px"
          src="image/exercises-101.png"
          alt="github"
        />
      </div>
      <div className="column">
        <Link
          href="https://github.com/padolabs/web-development-101/tree/master/1.0.0-introduction-to-web/1.1.0-exercises"
          target="blank"
        >
          Exercícios
        </Link>
        <br />
        <br />
      </div>
    </Slide>
  </Slide>
);

export default AboutGitCommands;
